// Request plugin.
import * as actionTypes from './interface/actionTypes'
import * as actions from './interface/actions'
import { injectAgentVersionHeader } from './interface/selectors'
import { defaultOptions, parseOptions } from './configs'
import makeRequest from './makeRequest'
import { getCpaasAgentHeaderValue } from './utils'

// Other plugins.
import { getPlatform } from '../auth/interface/selectors'
import { update as updateConfig } from '../config/interface/actions'
import { logManager } from '../logs'

// Utils.
import { mergeValues } from '../common/utils'

// Libraries.
import { call, put, takeEvery, select } from 'redux-saga/effects'
import { cloneDeep } from 'lodash/fp'

const pluginName = 'requests'
const log = logManager.getLogger('REQUESTS')

/*
 * HTTP request plugin.
 */
export default function request (options = {}) {
  options = mergeValues(defaultOptions, options)
  parseOptions(options)

  function * init () {
    yield put(updateConfig(options, pluginName))
  }

  return {
    sagas: [watchRequests],
    name: pluginName,
    init
  }
}

/*
 * Saga to watch for every request action.
 */
function * watchRequests () {
  yield takeEvery(actionTypes.REQUEST, handleRequest)
}

/*
 * Generator that handles a request action with standard HTTP handling features and reports
 * a requestFinished action when the request is done.
 *
 * @param {FluxStandardAction} action The action to handle.
 */
function * handleRequest (action) {
  const options = yield call(addVersionHeader, action.payload)

  const logOptions = cloneDeep(options)
  // When logging the Auth header, cut it off so that we can see the type of
  //    token but not the token itself. Depending on the type, it can contain
  //    a password.
  const authHeader = logOptions.headers.Authorization
  if (authHeader) {
    logOptions.headers.Authorization = authHeader.substring(0, 6) + '...'
  }
  log.debug(`Making REST request ${action.meta.requestId}.`, logOptions)

  // Make the request based on the action
  var result = yield call(makeRequest, options, action.meta.requestId)

  log.debug(`Received REST response ${action.meta.requestId}.`, result)

  yield put(actions.response(action.meta.requestId, result, !!result.error))
}

/**
 * If enabled, adds the Agent Version header to the REST request's options.
 * Relies on the "platform" being set in state to know which SDK is being used.
 * @method addVersionHeader
 * @param  {Object} options Request options.
 * @return {Object} The same request options but with one extra header (maybe).
 */
function * addVersionHeader (options) {
  const useCustomHeader = yield select(injectAgentVersionHeader)
  if (useCustomHeader) {
    const platform = yield select(getPlatform)

    // Assume request is for CPaaS platform, by default.
    const headerValue = getCpaasAgentHeaderValue(platform, options.url)

    // Note that the same headerName is used for all platforms & services.
    options = mergeValues(options, {
      headers: {
        'X-Cpaas-Agent': headerValue
      }
    })
  }

  return options
}

// begin-test-code
export const __testonly__ = { addVersionHeader, watchRequests, handleRequest }
// end-test-code
